<template>
  <div style="width:80%;margin:auto">
  <a-steps :current="activeKey - 0" size="small">
    <a-step title="" />
    <a-step title="" />
    <a-step title="" />
    <a-step title="" />
    <a-step title="" />
    <a-step title="" />
  </a-steps>
  </div>
  <div style="margin:30px 0">
  <a-form
    ref="formRef"
    :model="AddFormState"
    :rules="rules"
  >
        <a-tabs @change="tabsChangeHandler" :activeKey="activeKey" tab-position="left">
 <!-- ********************************************************* -->
          <a-tab-pane key="0" tab="" style="width:900px">
              <a-form-item label="" name="goods_name">
              <a-input v-model:value="AddFormState.goods_name" />
            </a-form-item>
            <a-form-item label="" name="goods_price">
              <a-input v-model:value="AddFormState.goods_price" />
            </a-form-item>
            <a-form-item label="" name="goods_number">
              <a-input v-model:value="AddFormState.goods_number" />
            </a-form-item>
            <a-form-item label="" name="goods_weight">
              <a-input v-model:value="AddFormState.goods_weight" />
            </a-form-item>
            <a-form-item label="" name="goods_cat" style="width:400px">
              <!--  -->
                <a-cascader
                v-model:value="cateOptions"
                :options="options"
                expand-trigger="hover"
                placeholder=""
                @change="selectChangeHandler"
                :fieldNames="{label:'cat_name' , value :'cat_id' , children:'children'}"
              />
              <!--  end -->
            </a-form-item>
          </a-tab-pane>
<!-- ***************************************** -->
          <a-tab-pane key="1" tab="">
            <a-button type="primary" @click="resetManyHandler"></a-button>
            <!--  -->
            <a-form-item v-for="item in manyList" :key="item.attr_id" :label="item.attr_name">
              <!--  -->
                <a-checkbox-group v-model:value="item.attr_vals" name="checkboxgroup" :options="item.attr_vals" />
            </a-form-item>
          </a-tab-pane>
 <!-- ********************************************************* -->
          <a-tab-pane key="2" tab=""  style="width:900px">
            <a-form-item v-for="item in onlyList" :key="item.attr_id" :label="item.attr_name">
              <!--  -->
                <a-input v-model:value="item.attr_vals" />
            </a-form-item>
            </a-tab-pane>
 <!-- ********************************************************* -->
          <a-tab-pane key="3" tab="">
            <a-upload
            action="http://106.52.84.103:8888/api/private/v1/upload"
            list-type="picture"
            @change="goodsPicUpload"
            @preview="picPreviewHandler"
            :headers="headersObj"
          >
              <a-button>
                <upload-outlined></upload-outlined>
                
              </a-button>
          </a-upload>
            </a-tab-pane>
 <!-- ********************************************************* -->
          <a-tab-pane :forceRender="true" key="4" tab=""  style="width:90%">
            <div ref='editor'></div>
            </a-tab-pane>
 <!-- ********************************************************* -->
          <a-tab-pane key="5" tab="">
            <a-button type="primary" @click="submitAddForm"></a-button>
            </a-tab-pane>
        </a-tabs>
    </a-form>
  </div>
</template>

<script>
import { UploadOutlined } from '@ant-design/icons-vue'

import { getCurrentInstance, onMounted, onBeforeUnmount } from 'vue'
import { reactive, ref } from '@vue/reactivity'
import { message } from 'ant-design-vue'
import WangEditor from 'wangeditor'
export default {
  components: {
    UploadOutlined
  },
  setup () {
    const { proxy } = getCurrentInstance()
    const activeKey = ref('0')
    // 
    const AddFormState = reactive({
      goods_name: '',
      goods_price: '',
      goods_number: '',
      goods_weight: '',
      goods_cat: [],
      attrs: [],
      pics: [],
      goods_introduce: ''
    })
    // 
    const rules = {
      goods_name: [{ required: true, message: '', trigger: 'blur' }],
      goods_price: [{ required: true, message: '', trigger: 'blur' }],
      goods_number: [{ required: true, message: '', trigger: 'blur' }],
      goods_weight: [{ required: true, message: '', trigger: 'blur' }],
      goods_cat: [{ required: true, message: '', trigger: 'blur' }]

    }
    // 
    const options = ref([])
    // 
    const cateOptions = ref([])
    // 
    const formRef = ref()
    // 
    const manyList = ref()
    // 
    const onlyList = ref()
    // 

    const selectChangeHandler = async value => {
      // ID
      const cateID = value[value.length - 1]
      // ID,
      AddFormState.goods_cat = value.toString()
      // 
      // 
      const { meta, data } = await proxy.$http.get(`categories/${cateID}/attributes`, {
        params: {
          sel: 'many'
        }
      })
      if (meta.status != 200) return message.error(meta.msg)
      manyList.value = data
      // attr_vals,,
      manyList.value.forEach(item => {
        item.attr_vals = item.attr_vals.split(',')
      })
      window.sessionStorage.manyList = JSON.stringify(manyList.value)
      // 
      const { data: onlyData } = await proxy.$http.get(`categories/${cateID}/attributes`, {
        params: {
          sel: 'only'
        }
      })
      onlyList.value = onlyData
    }
    // ref
    const editor = ref()
    let instance
    // 
    onMounted(async () => {
      const { data } = await proxy.$http.get('categories')
      options.value = data
      //   
      instance = new WangEditor(editor.value)
      instance.create()
    })
    // 
    onBeforeUnmount(() => {
      instance.destroy()
      instance = null
    })
    // 
    const tabsChangeHandler = (newKey) => {
      //  
      // activeKey.value = newKey

      // 
      formRef.value.validate()
        .then(() => {
        // v-model, 
          activeKey.value = newKey
        })
        .catch(() => {
          message.error('!')
        })
    }
    // 
    const resetManyHandler = () => {
      manyList.value = JSON.parse(window.sessionStorage.manyList)
    }
    // Axios,token token
    const headersObj = {
      Authorization: window.sessionStorage.token
    }

    // 
    const goodsPicUpload = ({ file }) => {
      // 
      if (file.status == 'done') {
        const obj = {
          pic: file.response.data.tmp_path
        }
        AddFormState.pics.push(obj)
      }
      // 
      if (file.status == 'removed') {
        // 
        const index = AddFormState.pics.findIndex(item => {
          return item.pic === file.response.data.tem_path
        })
        // 
        AddFormState.pics.splice(index, 1)
      }
    }
    // 
    const picPreviewHandler = (file) => {
      window.open(file.response.data.url)
    }
    // ,
    const submitAddForm = async () => {
      AddFormState.goods_introduce = instance.txt.html()
      manyList.value.forEach(item => {
        AddFormState.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals.toString()
        })
      })
      //    
      onlyList.value.forEach(item => {
        AddFormState.attrs.push({
          attr_id: item.attr_id,
          attr_value: item.attr_vals.toString()
        })
      })
      const { meta } = await proxy.$http.post('goods', AddFormState)
      if (meta.status != 201) return message.error(meta.msg)
      message.success(meta.msg)
      proxy.$router.push('/home/goods')
    }
    return {
      submitAddForm,
      tabsChangeHandler,
      headersObj,
      picPreviewHandler,
      selectChangeHandler,
      goodsPicUpload,
      resetManyHandler,
      activeKey,
      onlyList,
      manyList,
      editor,
      rules,
      formRef,
      cateOptions,
      options,
      AddFormState
    }
  }

}
</script>

<style lang="less" scoped>

</style>
